@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=eternal:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@700&display=swap");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-ibm-plex-mono: "IBM Plex Mono";
  --font-inter: Inter;
  --font-jetbrains-mono: "JetBrains Mono";
  --font-eternal: eternal;

  /* font sizes */
  --font-size-12xs: 5.57px;
  --font-size-11xs: 7.27px;
  --font-size-10xs: 8.31px;
  --font-size-9xs: 9.82px;
  --font-size-8xs: 11.55px;
  --font-size-7xs: 11.21px;
  --font-size-6xs: 11.3px;
  --font-size-5xs: 12px;
  --font-size-4xs: 13.2px;
  --font-size-3xs: 16px;
  --font-size-2xs: 18.83px;
  --font-size-xs: 20px;
  --font-size-sm: 22.17px;
  --font-size-base: 24px;
  --font-size-lg: 40px;

  /* Colors */
  --greyscale-w: #fff;
  --greyscale-k: #000;
  --color-y: #ffff50;
  --greyscale-g: #c4c4c4;
  --color-gray-200: #3d3737;
  --color-b: #4d7bff;
  --color-g: #7fff5b;

  /* Gaps */
  --gap-0: 0px;
  --gap-4xs: 2.08px;
  --gap-3xs: 2.23px;
  --gap-2xs: 3.3px;
  --gap-xs: 3.27px;
  --gap-sm: 6px;
  --gap-md: 6.55px;
  --gap-lg: 7.39px;
  --gap-xl: 7.47px;
  --gap-2xl: 8px;
  --gap-3xl: 14.78px;
  --gap-4xl: 16px;
  --gap-5xl: 24px;

  /* Paddings */
  --padding-3xs: 6.545454502105713px;
  --padding-2xs: 8.305343627929688px;
  --padding-xs: 13.199999809265137px;
  --padding-sm: 14.778182983398438px;
  --padding-md: 16px;
  --padding-lg: 16.610687255859375px;
  --padding-xl: 26.399999618530273px;
  --padding-2xl: 32px;

  /* border radiuses */
  --br-md: 4px;

  /* Effects */
  --s: 16px 12px 0px #000;
}
